
"use client";

import { useState, useEffect, useCallback } from 'react';
import { toast } from '@/hooks/use-toast';
import type { TokenBalance, WalletState } from '@/types';

const formatEth = (wei: string): number => {
  if (!wei || typeof wei !== 'string' || !wei.startsWith('0x')) {
    return 0;
  }
  return parseInt(wei, 16) / 10**18;
};

const initialWalletState: WalletState = {
  isConnected: false,
  account: null,
  balance: [],
  connectWallet: async () => {},
  disconnectWallet: () => {},
  loading: true, // Start with loading true
  error: null,
};

export const useWallet = (): WalletState => {
  const [isMounted, setIsMounted] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [account, setAccount] = useState<string | null>(null);
  const [balance, setBalance] = useState<TokenBalance[]>([]);
  const [loading, setLoading] = useState(true); // Initialize loading to true
  const [error, setError] = useState<string | null>(null);
  const { toast: anothertoast } = useToast(); // Renamed to avoid conflict if any, though `toast` is conventional

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const fetchBalance = useCallback(async (currentAccount: string) => {
    if (!isMounted || typeof window.ethereum === 'undefined' || !currentAccount) {
      if (isMounted) setError("Failed to fetch balance: Invalid parameters.");
      return;
    }
    // setLoading(true); // setLoading is managed by callers or initial state
    try {
      const rawBalance = await window.ethereum.request({
        method: 'eth_getBalance',
        params: [currentAccount, 'latest'],
      });
      const nativeBalance = formatEth(rawBalance as string);
      
      if (isMounted) {
        setBalance([{
          symbol: 'ETH', 
          name: 'Ethereum',
          balance: nativeBalance,
          valueUSD: undefined, // Price fetching is separate
        }]);
        setError(null);
      }
    } catch (err: any) {
      console.error("Error fetching balance:", err);
      if (isMounted) {
        setError("Failed to fetch balance.");
        setBalance([]);
      }
    } finally {
      // setLoading(false); // setLoading is managed by callers or effects
    }
  }, [isMounted]);

  const disconnectWallet = useCallback(() => {
    if (!isMounted) return;
    setIsConnected(false);
    setAccount(null);
    setBalance([]);
    if (typeof localStorage !== 'undefined') {
      localStorage.removeItem('aethermind_wallet_account');
    }
    anothertoast({ title: "Wallet Disconnected" });
    setError(null);
    // Event listener cleanup is handled in the dedicated useEffect
  }, [isMounted, anothertoast]);

  const handleAccountsChanged = useCallback((accounts: string[]) => {
    if (!isMounted) return;
    if (accounts.length === 0) {
      disconnectWallet(); // Safely uses memoized disconnectWallet
    } else if (accounts[0] !== account) {
      const newAccount = accounts[0];
      setAccount(newAccount);
      setIsConnected(true); 
      setLoading(true);
      fetchBalance(newAccount).finally(() => {
        if (isMounted) setLoading(false);
      });
      if (typeof localStorage !== 'undefined') {
        localStorage.setItem('aethermind_wallet_account', newAccount);
      }
      anothertoast({ title: "Account Switched", description: `Switched to account: ${newAccount.substring(0,6)}...${newAccount.substring(newAccount.length-4)}` });
    }
  }, [isMounted, account, fetchBalance, disconnectWallet, anothertoast]);

  const connectWallet = useCallback(async () => {
    if (!isMounted) return;
    if (typeof window.ethereum === 'undefined') {
      setError("Metamask is not installed. Please install Metamask to connect your wallet.");
      anothertoast({ title: "Metamask Not Found", description: "Please install Metamask and try again.", variant: "destructive" });
      return;
    }
    
    setLoading(true);
    setError(null);
    try {
      const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' }) as string[];
      if (accounts.length > 0) {
        const currentAccount = accounts[0];
        setAccount(currentAccount);
        setIsConnected(true);
        await fetchBalance(currentAccount); // Safely uses memoized fetchBalance
        if (typeof localStorage !== 'undefined') {
          localStorage.setItem('aethermind_wallet_account', currentAccount);
        }
        anothertoast({ title: "Wallet Connected", description: `Connected to account: ${currentAccount.substring(0,6)}...${currentAccount.substring(currentAccount.length-4)}` });
      } else {
        setError("No accounts found. Please ensure your Metamask wallet is set up.");
        anothertoast({ title: "Connection Failed", description: "No accounts found in Metamask.", variant: "destructive" });
        setIsConnected(false);
        setAccount(null);
        setBalance([]);
      }
    } catch (err: any) {
      console.error("Error connecting wallet:", err);
      const errorMessage = err.message || "Failed to connect wallet. User might have rejected the request.";
      setError(errorMessage);
      anothertoast({ title: "Connection Failed", description: errorMessage, variant: "destructive" });
      setIsConnected(false);
      setAccount(null);
      setBalance([]);
    } finally {
      if (isMounted) setLoading(false);
    }
  }, [isMounted, fetchBalance, anothertoast]);

  // Effect for restoring connection
  useEffect(() => {
    if (!isMounted || typeof window.ethereum === 'undefined') {
        // If not mounted or ethereum not available, ensure loading is false if it wasn't a direct connect attempt
        if (!loading) setLoading(false); // Only set to false if not already true from a connect attempt
        return;
    }

    let isActive = true;
    const attemptConnectionRestore = async () => {
      if (!account && !isConnected) { // Only attempt if not already connected/connecting
        setLoading(true);
        const storedAccount = typeof localStorage !== 'undefined' ? localStorage.getItem('aethermind_wallet_account') : null;
        if (storedAccount) {
          try {
            const accs = await window.ethereum.request({ method: 'eth_accounts' }) as string[];
            if (isActive && accs.length > 0 && accs.includes(storedAccount)) {
              setAccount(storedAccount);
              setIsConnected(true);
              await fetchBalance(storedAccount);
            } else if (isActive) {
              if (typeof localStorage !== 'undefined') localStorage.removeItem('aethermind_wallet_account');
              // No need to set isConnected/account to false/null, they are already so.
            }
          } catch (err) {
            if (isActive) {
              console.error("Error restoring wallet session:", err);
              if (typeof localStorage !== 'undefined') localStorage.removeItem('aethermind_wallet_account');
              setError("Failed to restore wallet session.");
            }
          }
        }
        if (isActive) setLoading(false);
      } else {
         if(isActive) setLoading(false); // Already connected or account set, ensure loading is false
      }
    };

    attemptConnectionRestore();
    
    return () => {
      isActive = false;
    };
  }, [isMounted, fetchBalance, account, isConnected]); // Added account & isConnected to deps

  // Effect solely for managing the accountsChanged listener
  useEffect(() => {
    if (!isMounted || typeof window.ethereum === 'undefined' || !window.ethereum.on) {
      return;
    }

    let isActive = true;
    const currentHandler = (accounts: string[]) => {
      if (isActive) {
        handleAccountsChanged(accounts);
      }
    };

    if (isConnected && account) { // Only add listener if truly connected
      window.ethereum.on('accountsChanged', currentHandler);
    }

    return () => {
      isActive = false;
      if (typeof window.ethereum.removeListener === 'function') {
        // No need to check isConnected/account here, just try to remove if the function exists
        window.ethereum.removeListener('accountsChanged', currentHandler);
      }
    };
  }, [isMounted, isConnected, account, handleAccountsChanged]);


  if (!isMounted && typeof window === 'undefined') {
    return { ...initialWalletState, loading: true, connectWallet: async () => {}, disconnectWallet: () => {} };
  }
  
  if (!isMounted) {
      return { ...initialWalletState, loading: true, connectWallet: async () => {}, disconnectWallet: () => {} };
  }

  return { isConnected, account, balance, connectWallet, disconnectWallet, loading, error };
};

export const formatBalanceForAI = (balance: TokenBalance[]): string => {
  if (!balance || balance.length === 0) return "No token holdings detected or wallet not connected.";
  
  const nativeCurrency = balance.find(token => token.symbol === 'ETH'); // Assuming ETH as primary, adjust if needed
  
  if (nativeCurrency && nativeCurrency.balance > 0) {
    return `${nativeCurrency.balance.toFixed(4)} ${nativeCurrency.symbol}. Other token balances are not itemized for this prompt. Focus on native currency for now.`;
  } else if (nativeCurrency && nativeCurrency.balance === 0) {
    return `0.0000 ${nativeCurrency.symbol}. Other token balances are not itemized for this prompt.`;
  }
  
  return "Native currency balance not available or is zero. Other token balances not itemized for this prompt.";
};
